.info-board .board-wrap {
  display: flex;
  flex-direction: column;
  row-gap: 32px;
  position: relative;
}
.info-board .board-wrap::before {
  content: "";
  display: block;
  position: absolute;
  top: auto;
  bottom: calc(100% + 24px);
  left: calc(50% - 60px);
  width: 120px;
  height: 120px;
  background-image: var(--image);
  background-repeat: no-repeat;
  background-position: 50%;
  background-size: cover;
}
.info-board .board-wrap .board-subject {
  font-size: 46px;
  font-weight: 600;
  line-height: 70px;
  color: var(--color-base-040);
  text-align: center;
}
.info-board .board-wrap .board-subject .wbr {
  display: block;
}
.info-board .board-wrap .board-content .board-title {
  font-size: 28px;
  font-weight: 600;
  line-height: 42px;
  color: var(--color-base-010);
  text-align: center;
}
.info-board .board-wrap .board-content .board-title .wbr {
  display: block;
}
.info-board .board-wrap .board-content .board-text {
  margin-top: 12px;
  font-size: 18px;
  line-height: 28px;
  color: var(--color-base-030);
  text-align: center;
}
.info-board .board-wrap .board-content .board-text .para {
  display: block;
}
.info-board .board-wrap .board-content .board-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  row-gap: 4px;
  margin-top: 32px;
  padding: 20px;
  background: var(--color-base-050);
}
.info-board .board-wrap .board-content .board-box span {
  font-size: 18px;
  line-height: 28px;
  color: var(--color-base-020);
}
@media (max-width: 1023px) {
  .info-board[data-text]::before {
    margin: 0 0 92px;
    font-size: 64px;
    line-height: 64px;
  }
  .info-board .board-wrap {
    row-gap: 24px;
  }
  .info-board .board-wrap::before {
    bottom: calc(100% + 16px);
    width: 100px;
    height: 100px;
  }
  .info-board .board-wrap .board-subject {
    font-size: 28px;
    line-height: 42px;
  }
  .info-board .board-wrap .board-content .board-title {
    font-size: 22px;
    line-height: 32px;
  }
  .info-board .board-wrap .board-content .board-text {
    margin-top: 12px;
    font-size: 16px;
    line-height: 24px;
    word-break: keep-all;
  }
  .info-board .board-wrap .board-content .board-box {
    margin-top: 24px;
    padding: 12px;
  }
}