/********************************************************************************************************************************

 * 공통

********************************************************************************************************************************/
.blind {
	overflow: hidden;
	position: absolute;
	top: -9999em;
	left: -9999em;
	width: 1px;
	height: 1px;
	margin: 0 !important;
	padding: 0 !important;
	line-height: 1px;
	text-indent: -9999em;
}

#page {
	flex: 1 1 100%;
	display: flex;
	flex-direction: column;
	row-gap: 48px;
	width: 100%;
	max-width: 1360px;
	margin: 0 auto;

	.page-head {
		.heading-l { text-align: center; }
	}

	.page-body {
		>.title-l { text-align: center; }

		>.body-l { text-align: center; }

		.data-finder {
			margin: 0 0 24px;
		}

		.pagination {
			margin: 40px 0 0;
		}

		.button-display {
			margin: 40px 0 0;

			.button-area {
				gap: 12px;
				
				.btn-a {
					min-width: 180px;
				}
			}
		}

		@include respond-below(tablet) {
			.button-display {
				// margin: 40px 0 0;
	
				.button-area {
					// gap: 12px;
					
					.btn-a {
						flex: 1 1 100%;
						min-width: auto;
					}
				}
			}
		}
	}
}


.wbr {
	display: block;
}


