/********************************************************************************************************************************

 * 게시판

********************************************************************************************************************************/
.board-view {
	max-width: 960px;
	margin: 0 auto;

	.board-view-inform {
		display: flex;
		// justify-content: center;
		// align-items: center;
		flex-direction: column;
		row-gap: 16px;
		border-bottom: 1px solid var(--color-base-010);
		padding-bottom: 40px;

		.board-subject {			
			font-size: 40px;
			font-weight: 700;
			line-height: 60px;
			color: var(--color-base-010);

			.em {
				padding-right: 16px;
				color: var(--color-base-010);
			}
		}

		.board-date {
			font-size: 18px;
			line-height: 28px;
			color: var(--color-base-040);
		}

		// .board-dataset {
		// 	flex: 1 1 100%;
		// 	display: flex;
		// 	align-items: center;
		// 	gap: 24px;

		// 	.date {
		// 		font-size: 18px;
		// 		line-height: 28px;
		// 		color: var(--color-base-010);
		// 	}
		// }
	}

	.board-view-content {
		max-width: 1020px;
		margin: 0 auto;
		padding: 40px 0;
		font-size: 18px;
		line-height: 28px;

		&:has(.camp-board) {
			max-width: 1400px;
			padding: 20px 0;
		}

		p, span {
			font-size: inherit;
			line-height: inherit;
		}

		p {
			&:has(iframe),
			&.video {
				position: relative;
				width: 100%;
				height: 0;
				padding-bottom: 56.25%;
				
				iframe {
					position: absolute;
					top: 0;
					left: 0;
					z-index: 1;
					width: 100%;
					height: 100%;
				}
			}
		}

		img {
			max-width: 100%;
			height: auto;
		}

		figure.image {
			text-align: center;

			img {
				cursor: pointer;
			}
		}

		.para {
			color: var(--color-base-010);
			text-align: center;
		}
	}

	.board-view-slides {
		overflow: hidden;
		position: relative;
		margin: 24px 0;

		&::after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			right: 0;
			left: auto;
			z-index: 1;
			width: 200px;
			height: 184px;
			background: linear-gradient(90deg, rgba(255, 255, 255, 0.00) 0%, #FFF 100%);
		}

		.slide-list {
			.slide-item {
				width: 200px;
				height: 184px;

				a {
					display: block;
					height: 100%;
				}

				img {
					max-width: 100%;
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}

		[class*="swiper-button"] {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			top: calc(50% - 20px);
			z-index: 2;
			width: 40px;
			height: 40px;
			border-radius: 50%;
			background: rgba(0, 0, 0, 0.50);
			cursor: pointer;

			&::before {
				content: '';
				display: block;
				width: 28px;
				height: 28px;
				mask-repeat: no-repeat;
				mask-size: cover;
				mask-position: 50%;
				background-color: var(--color-base-100);
			}

			.btn-text {
				@include hidden;
			}

			&.swiper-button-disabled {
				display: none;
			}

			&.swiper-button-next {
				right: 0;

				&::before { mask-image: icon(chevron-right); }
			}
	
			&.swiper-button-prev {
				left: 0;

				&::before { mask-image: icon(chevron-left); }
			}
		}

		.swiper-scrollbar {
			position: static;
			height: 2px;
			margin-top: 24px;
			border-radius: 0;
			background-color: rgba(0, 0, 0, 0.1);

			.swiper-scrollbar-drag {
				background-color: var(--color-base-010);
			}
		}
	}

	.board-bottom {
		position: relative;
		padding: 40px 0 0;
		border-top: 1px solid var(--color-base-010);

		.navi-list {
			display: flex;
			justify-content: space-between;
			
			.navi-item {
				display: flex;
				column-gap: 8px;
				max-width: 370px;
				padding: 17px 0;

				+.navi-item {
					border-top: 0;
				}

				&:first-child {
					&::before {
						content: '';
						flex-shrink: 0;
						display: block;
						width: 28px;
						height: 28px;
						mask: icon(arrow-left);
						background-color: var(--color-base-010);
					}
				}

				&:last-child {
					&::after {
						content: '';
						flex-shrink: 0;
						display: block;
						width: 28px;
						height: 28px;
						mask: icon(arrow-right) no-repeat;
						background-color: var(--color-base-010);
					}
				}

				.head {
					@include respond-above(tablet) {
						@include hidden;
					}
				}

				.body {
					font-weight: 600;
					font-size: 20px;
					line-height: 30px;
					color: var(--color-base-010);
					@include ellipsis(1);
					
					.em {
						padding-right: 8px;
						color: var(--color-base-010);
					}
				}
			}

			+.button-area {
				position: absolute;
				bottom: 0;
				left: calc(50% - 90px);
				z-index: 1;
				margin: 0;

				.btn-a {
					min-width: 180px;

					&:only-child {
						flex: 1 1 100%;
					}
				}
			}
		}
	}

	.file-upload-result {
		max-width: 1020px;
		margin: 40px auto 0;
		padding: 24px;
		background-color: var(--color-base-090);

		+.board-content {
			margin-top: 40px;
		}
	}

	@include respond-below(tablet) {
		.board-view-inform {
			align-items: flex-start;
			padding-bottom: 32px;

			.board-subject {
				font-size: 24px;
				line-height: 36px;
			}

			.date {
				font-size: 16px;
				line-height: 24px;
			}
	
		}
	
		.board-view-content {
			width: 100%;
			padding: 32px 0;
			font-size: 16px;
			line-height: 24px;
		}

		// .board-view-slides {
		// 	&::after {
		// 		width: 56px;
		// 		height: 120px;
		// 	}

		// 	.slide-list {
		// 		.slide-item {
		// 			width: 130px;
		// 			height: 120px;
		// 		}
		// 	}

		// 	[class*="swiper-button"] {
		// 		@include hidden;
		// 	}
		// }
	
		.board-bottom {
			padding: 0;
	
			.navi-list {
				flex-direction: column;
				position: relative;
				z-index: 1;
	
				.navi-item {
					flex: 1 1 100%;
					max-width: 100%;
					padding: 20px 0;
					
					+.navi-item {
						border-top: 1px solid var(--color-base-010);
					}
	
					&:first-child {
						&::before {
							display: none;
						}
					}
	
					&:last-child {
						&::after {
							display: none;
						}
					}
	
					.head {
						flex-shrink: 0;
						font-size: 16px;
						line-height: 24px;
						color: var(--color-base-010);
					}
	
					.body {
						font-size: 16px;
						line-height: 24px;
					}
				}
	
				+.button-area {
					position: static;
					margin-top: 32px;

					.btn-a {

					}
				}
			}
		}
	
		.file-upload-result {
			padding: 16px;

			+.board-content {
				margin-top: 24px;
			}
		}
	}
}

.file-upload-result {
	box-sizing: border-box;

	.file-upload-wrap {
		display: flex;
		align-items: baseline;
		column-gap: 12px;

		.head {
			flex-shrink: 0;
			font-size: 18px;
			font-weight: 600;
			line-height: 28px;
			color: var(--color-base-010);
		}

		.body {
			display: inline-flex;
			flex-direction: column;

			.file-name {
				font-size: 18px;
				line-height: 28px;
				text-decoration-line: underline;
				color: var(--color-primary-050);
			}
		}
	}

	@include respond-below(tablet) {
		.file-upload-wrap {
			.head {
				font-size: 16px;
				line-height: 24px;
			}

			.body {
				.file-name {
					font-size: 16px;
					line-height: 24px;
				}
			}
		}
	}
}

// share-util
.share-util {
	position: fixed;
	top: 32%;
	right: 70px;
	z-index: 10;
	display: flex;
	flex-direction: column;
	row-gap: 16px;
	width: 56px;

	[class*=btn-share] {
		display: inline-flex;
		align-items: center;
		justify-content: center;
		width: 56px;
		height: 56px;
		box-sizing: border-box;
		border: 1px solid var(--color-base-080);
		border-radius: 50%;
		background-color: var(--color-base-095);

		&::before {
			content: '';
			display: block;
			width: 24px;
			height: 24px;
            background-color: var(--color-base-000);
			background-size: 24px;
			background-position: center;
			background-repeat: no-repeat;
		}

		.btn-text {
			@include hidden;
		}
	}

	.btn-share-url {
		&::before {
			mask: icon(link);
		}

		.btn-text {
			@include hidden;
		}
	}

	.btn-share-kakaotalk {
		&::before {
            mask: icon(kakaotalk);
		}

		.btn-text {
			@include hidden;
		}
	}

}


// gallery-display
.gallery-display {
	&:not(.masonry) {
		.gallery-list {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			row-gap: 32px;
			column-gap: 32px;
	
			.gallery-item {
				width: calc((100% - 64px) / 3);
				transition: all .3s ease;
	
				.gallery-wrap {
					display: flex;
					flex-direction: column;
					row-gap: 16px;
	
					&:hover {
						.gallery-thumb {
							.image {
								transform: scale(1.05);
								will-change: transform;
							}
						}

						.gallery-inform {
							.subject {
								text-decoration: underline;
							}	
						}
					}
	
					.gallery-thumb {
						overflow: hidden;
						width: 100%;
						// height: 324px;
						background-color: var(--color-base-030);
		
						.image {
							width: 100%;
							height: 100%;
							object-fit: cover;
							transition-property: all;
							transition-duration: 0.3s;
							transition-timing-function: ease-out;
							transition-delay: unset;
						}
					}
	
					.gallery-inform {
						display: flex;
						flex-wrap: wrap;
						row-gap: 8px;

						.subject {
							flex: 1 1 100%;
							display: flex;
							flex-direction: column;
		
							.em {
								flex-shrink: 0;
								font-weight: 600;
								font-size: 18px;
								line-height: 28px;
								color: var(--color-base-040);
							}
		
							.name {
								font-weight: 600;
								font-size: 20px;
								line-height: 30px;
								color: var(--color-base-010);
								@include ellipsis(2);
							}
						}

						.subtext {
							flex: 1 1 100%;
							line-height: 20px;
							@include ellipsis(2);
						}
		
						.date {
							font-size: 14px;
							line-height: 20px;
							color: var(--color-base-040);
						}

						.view {
							margin-left: auto;
							font-size: 14px;
							line-height: 20px;
							color: var(--color-base-040);
						}
					}
				}
			}
		}
	
		@include respond-below(tablet) {
			.gallery-list {
				.gallery-item {
					width: calc((100% - 32px) / 2);
				}
			}
		}
	
		@include respond-below(mobile) {
			.gallery-list {
				flex-direction: column;
				row-gap: 24px;
	
				.gallery-item {
					width: 100%;
	
					.gallery-wrap {
						.gallery-inform {
							.subject {
								column-gap: 4px;
			
								.em {
									font-size: 16px;
									line-height: 24px;
								}
			
								.name {
									font-size: 16px;
									line-height: 24px;
								}
							}
			
							.date {
								margin-top: 6px;
							}
						}
					}
				}
			}
		}
	}

	&.masonry {
		.gallery-list {
			.gallery-item {
				height: auto;
				will-change: transform;
				
				&:nth-of-type(n+5) {
					padding-top: 60px;
				}
				
				.gallery-wrap {
					display: flex;
					flex-direction: column;
					row-gap: 16px;

					&:hover {
						.gallery-thumb {
							img {
								transform: scale(1.05);
							}
						}
					}

					.gallery-thumb {
						overflow: hidden;
						background-color: var(--color-base-090);
						// aspect-ratio: 1800 / 1350;
						// aspect-ratio: 4 / 3;

						img {
							width: 100%;
                            height: auto;
                            object-fit: cover;
							transition-property: all;
							transition-duration: 0.3s;
							transition-timing-function: ease-out;
							transition-delay: unset;
						}
					}

					.gallery-inform {
						display: flex;
						flex-wrap: wrap;
						row-gap: 8px;

						.subject {
							flex: 1 1 100%;
							display: flex;
							flex-direction: column;
		
							.em {
								flex-shrink: 0;
								font-weight: 600;
								font-size: 18px;
								line-height: 28px;
								color: var(--color-base-040);
							}
		
							.name {
								font-weight: 600;
								font-size: 20px;
								line-height: 30px;
								color: var(--color-base-010);
								@include ellipsis(2);
							}
						}

						.subtext {
							flex: 1 1 100%;
							line-height: 20px;
							@include ellipsis(2);
						}
		
						.date {
							font-size: 14px;
							line-height: 20px;
							color: var(--color-base-040);
						}

						.view {
							margin-left: auto;
							font-size: 14px;
							line-height: 20px;
							color: var(--color-base-040);
						}
					}
				}
			}
		}
	}
}

// faq
.accordion-list {
	border-top: 1px solid var(--color-base-040);
	border-bottom: 1px solid var(--color-base-040);

	.accordion-item {
		+.accordion-item {
			border-top: 1px solid var(--color-base-040);
		}

		&.active {
			.accordion-head {
				.accordion-subject {
					font-weight: 600;

					&::after {
						mask-image: icon(chevron-up);
					}
				}
			}

			.accordion-content {
				height: auto;
				max-height: 1000vh;
				transition: max-height 1.5s ease-in-out;
			}
		}

		.accordion-head {
			display: flex;
			flex-direction: row;
			column-gap: 48px;
			row-gap: 12px;
			position: relative;			
			padding: 30px 48px;

			.accordion-caption {
				display: flex;
				align-items: center;
				column-gap: 48px;
				
				.abbr {
					width: 12px;
					text-align: center;
					font-weight: 600;
					line-height: 24px;
					color: var(--color-base-010);
				}

				.accordion-text {
					width: 87px;
					font-size: 18px;
					line-height: 28px;
					color: var(--color-base-010);
				}
			}

			.accordion-subject {
				flex: 1 1 100%;
				display: flex;
				justify-content: space-between;
				align-items: center;
				column-gap: 48px;
				font-size: 20px;
				line-height: 30px;
				font-weight: 500;
				color: var(--color-base-010);

				&::after {
					content: '';
					flex-shrink: 0;
					display: block;
					width: 30px;
					height: 30px;
					mask-repeat: no-repeat;
					mask-position: center;
					mask-size: cover;
					mask-image: icon(chevron-down);
					background-color: var(--color-base-010);
				}

				.btn-accordion {
					text-align: left;

					&::after {
						content: '';
						display: block;
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						z-index: 1;
						width: 100%;
						height: 100%;
					}
				}
			}
		}
		
		.accordion-content {
			overflow: hidden;
			max-height: 0;
			transition: max-height 0.5s cubic-bezier(0, 1, 0, 1);

			>.inner {
				display: flex;
				flex-direction: row;
				flex-wrap: wrap;
				column-gap: 48px;
				padding: 30px 48px;
				background-color: var(--color-base-090);
			}

			.abbr {
				flex-shrink: 0;
				font-weight: 600;
				line-height: 24px;
				color: var(--color-primary-040);
			}

			.para {
				flex: 1 1 100%;
				max-width: calc(100% - 60px);
				font-size: 18px;
				line-height: 28px;
				color: var(--color-base-010);
			}

			.link {
				text-decoration: underline;
				color: var(--color-primary-040);
			}

			.ico-link {
				display: inline-flex;
				align-items: center;
				color: var(--color-primary-040);
				text-decoration: underline;

				&::after {
					content: '';
					display: inline-block;
