// submit-form
.submit-form {
	&.style-a {
		box-shadow: inset 0 1px 0 var(--color-base-010);

		.form-list {
			padding: 20px 0;

			.form-item {
				display: flex;
				flex-direction: row;
				align-items: flex-start;
				// align-items: center;
				// row-gap: 8px;
				column-gap: 40px;
				padding: 20px 0;

				@include most-outer-selector(".forms-read") {
					align-items: center;
				}

				.form-head {
					flex-shrink: 0;
					display: flex;
					align-self: flex-start;
					width: 102px;
					min-height: 52px;

					@include most-outer-selector(".forms-read") {
						min-height: auto;
					}

					.form-subject {
						align-self: center;
						color: var(--color-base-010);
						font-size: 18px;
						font-weight: 600;
						line-height: 1.3;
						// line-height: 52px;
						word-break: auto-phrase;

						.required {
							&::after {
								content: '*';
								display: inline-block;
								margin-left: 2px;
								font-weight: 600;
								line-height: 24px;
								color:var(--color-system-010);
							}
						}
					}
				}

				.form-body {
					flex: 1 1 100%;
					display: flex;
					flex-wrap: wrap;
					max-width: calc(100% - (102px + 40px));

					.form {
						flex: 1 1 100%;
					}
				}
			}
		}

		@include respond-below(tablet) {
			.form-list {
				padding: 16px 0;
		
				.form-item {
					flex-direction: column;
					row-gap: 8px;
					padding: 16px 0;
	
					.form-head {
						width: 100%;

						.form-subject {
							font-size: 18px;
							line-height: 28px;
						}
					}
	
					.form-body {
						width: 100%;
						max-width: 100%;
					}
				}
			}			
		}
	}

	&.forms-read {
		.form-list {
			padding: 0;

			.form-item {
				+.form-item {
					border-top: 1px solid var(--color-base-080);
				}

				.form-head {
					.form-subject {
						line-height: 28px;
					}
				}

				.form-body {
					.form-content {
						line-height: 24px;
						color: var(--color-base-020);

						&.file {
