.data-board {
	.board-list {
		display: flex;
		flex-direction: column;
		row-gap: 12px;

		.board-item {
			flex: 1 1 100%;

			.board-wrap {
				display: flex;
				align-items: flex-start;
				column-gap: 12px;
				padding: 24px;
				border-radius: 8px;
				border: 1px solid transparent;
				box-sizing: border-box;
				background-color: var(--color-base-090);
				font-size: 16px;
				line-height: 24px;
				transition: background-color .3s ease-in-out, border-color .3s ease-in-out;

