		&:not(:first-child) {
			margin: 48px 0 0;
		}

		>.order-item {
			.subject {
				margin: 12px 0;
				font-size: 22px;
				font-weight: 600;
				line-height: 32px;
				color: var(--color-base-010);
				
				@include respond-below(tablet) {
					font-size: 20px;
					line-height: 30px;
				}
			}

			.summary {
				margin-top: 8px;
				font-size: 16px;
				line-height: 24px;
				color: var(--color-base-020);

				&:not(:last-child) {
					margin: 8px 0;
				}
