				margin: 0;

				.order-item {
					margin: 4px 0;
				}
			}

			ol.order-list {
				.order-item {
					font-size: 16px;
					line-height: 24px;
					color: var(--color-base-020);
				}
			}

			.data-list {
				margin: 8px 0;
			}
		}
	}

	.table-area {
		width: 100%;
		margin: 16px 0;
		
		@include respond-below(tablet) {
			@include overflow-scroll(default, dark, large, x);
		}
		
		table {
			table-layout: fixed;
			position: relative;
			width: 100%;
			border-spacing: 0;
			border-collapse: collapse;
			border-right: hidden;
			border-left: hidden;

			@include respond-below(tablet) {
				min-width: 448px;
			}

			&::before {
				content: '';
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 1px;
				background-color: var(--color-base-010);
			}

			thead {
				tr {
					th {
						padding: 12px;
						border: 1px solid var(--color-base-090);
						border-bottom: 0;
						background-color: var(--color-base-080);
						font-size: 16px;
						font-weight: 500;
						line-height: 24px;
						color: var(--color-base-020);
						text-align: center;
						word-break: keep-all;

						@include respond-below(tablet) {
							padding: 10px 8px;
							font-size: 14px;
							line-height: 20px;
						}
					}
				}
			}

			tbody {
				tr {
					td {
						padding: 12px;
						border: 1px solid var(--color-base-090);
						font-size: 16px;
						line-height: 24px;
						color: var(--color-base-020);
						vertical-align: middle;

						@include respond-below(tablet) {
							padding: 10px;
							font-size: 14px;
							line-height: 20px;
							vertical-align: top;
						}
					}
				}
			}
		}
	}
}

