.nodata-board {
	border: 1px solid var(--color-base-080);

	.board-wrap {
		padding: 60px 0;

		.subject {
			font-size: 20px;
            line-height: 32px;
            color: var(--color-base-000);
			text-align: center;
		}
	}
}

.data-list {
	&[class*="bullet"] {
		.data-item {
			flex: 1 1 100%;
			position: relative;
			padding-left: var(--padding-left);
			font-size: var(--font-size);
			line-height: var(--line-height);
			color: var(--color);
			word-break: keep-all;

			&:not(:first-child) {
				margin-top: var(--margin-top);
			}

			&:not(.astr) {
				&::before {
					content: '';
					display: block;
					position: absolute;
					top: calc((var(--line-height) / 2) - (var(--bullet-height) / 2 ));
					left: 0;
					width: var(--bullet-width);
					height: var(--bullet-height);
					border-radius: var(--border-radius);
					background-color:  var(--color);
				}
			}

			&.astr {
				margin-top: 4px;
				padding-left: 0;
				color: var(--color-base-040);

				&::before {
					content: '* ';
				}

			}

			strong {
				font-weight: 600;
			}
		}
	}

	&.bullet-dash {
		--margin-top: 4px;
		--padding-left: 14px;
		--font-size: 16px;
		--line-height: 24px;
		--color: var(--color-base-020);

		--bullet-width: 4px;
		--bullet-height: 1px;
		--border-radius: 0;
	}

	&.bullet-dot {
		--margin-top: 4px;
        --padding-left: 14px;
        --font-size: 16px;
        --line-height: 24px;
        --color: var(--color-base-020);

		--bullet-width: 4px;
		--bullet-height: 4px;
		--border-radius: 50%;
	}
}

.agreement-display {
