// agreement-terms
.agreement-terms {
	display: flex;
	flex-direction: column;
	row-gap: 20px;

	.terms-head {
		padding: 24px 18px;
		background-color: rgba(229, 0, 96, 0.05);
		
		.form.checkbox {
			.form-text {
				word-break: keep-all;
			}
		}
	}

	.terms-body {
		padding: 0 12px 0 20px;

		.terms-list {
			display: flex;
			flex-direction: column;
			row-gap: 12px;

			.terms-item {
				flex: 1 1 100%;

				.terms-wrap {
